import { types } from "../types/types";

export const authReducer = ( state, action ) => {
    switch (action.type) {
        case types.login:
            return {
                ...action.payload // action.payload = { user: "yael", logged: true }
            };
        case types.logout:
            return {
                ...action.payload
            };
        default:
            return state;
    }
}
