import React from 'react';
import { Link } from 'react-router-dom';
// import image from `../assets/heroes/${id}.jpg`;

export const HeroCard = ({
    id,
    superhero,
    publisher,
    alter_ego,
    first_appearance,
    characters
}) => {
    const textSomeStyle = {
        display: '-webkit-box',
        webkitLineClamp: '1',
        webkitBoxOrient: 'vertical',
        overflow: 'hidden',
        textOverflow: 'ellipsis'
    }
    return (
        <div className="card mb-3" style={{maxWidth: "350px"}}>
            <div className="row g-0">
                <div className="col-md-4">
                    <Link to={`/hero/${id}`}>
                        <img src={`/heroes-app/assets/heroes/${id}.jpg`} className="img-fluid rounded-start" alt={id} />
                    </Link>
                </div>
                <div className="col-md-8">
                    <div className="card-body">
                        <Link to={`/hero/${id}`} className="text-reset text-decoration-none">
                            <h5 className="card-title">{ superhero }</h5>
                        </Link>
                        <p className="card-text">{ alter_ego }</p>
                        {characters !== alter_ego && <p className="card-text" style={textSomeStyle}><small>{ characters }</small></p>}
                        <p className="card-text"><small className="text-muted" >{ first_appearance }</small></p>
                    </div>
                </div>
            </div>
        </div>
    )
}
