import React from 'react'
import { heroes } from '../data/heroes'
import { HeroCard } from './HeroCard'

export const HeroFinder = ({ inputEntry }) => {
    const filteredHeroes = heroes.filter( hero => {
        const heroLowerCase = hero.superhero.toLowerCase();
        const alter_egoLowerCase = hero.alter_ego.toLowerCase();
        inputEntry = inputEntry.toLowerCase();
        return (heroLowerCase.includes(inputEntry) || alter_egoLowerCase.includes(inputEntry)) 
    })
    return (
        <div className="d-flex flex-wrap">
            {
            filteredHeroes.map(( hero ) => (
                <HeroCard key={hero.id} {...hero}/>
            ))
            }
        </div>
    )
}
