import React from 'react'
import { Redirect, useParams } from 'react-router'
import { getHeroById } from '../helpers/getHeroById';

export const HeroScreen = ({ history }) => {
    const { heroId } = useParams();
    const hero = getHeroById(heroId)
    
    if ( !hero ) {
        return <Redirect to='/'/> // el return es necesario porque todo componente tiene que retornar algo
    }

    const {
        superhero,
        publisher,
        alter_ego,
        first_appearance,
        characters
    } = hero;

    const handleClick = () => {
        history.goBack();
    }
    
    // heroes-app is been added for github pages
    return (
        <div className="row mt-5">
            <div className="col-4">
                <img
                    src={`/heroes-app/assets/heroes/${heroId}.jpg`} /* heroes-app is been added for github pages */
                    alt={ superhero }
                    className="img-thumbnail animate__animated animate__fadeIn"
                />
            </div>
            <div className="col-4">
                <ul className="list-group list-group-flush">
                    <li className="list-group-item"> Alter ego: <b> { alter_ego } </b> </li>
                    <li className="list-group-item"> Publisher: <b> { publisher } </b> </li>
                    <li className="list-group-item"> First appearance: <b> { first_appearance } </b> </li>
                </ul>
                <div className="ms-3 mt-4">
                    <h5> Characters </h5>
                    <p>{ characters }</p>
                </div>

                <button
                    className="btn btn-outline-info ms-3 mt-4"
                    onClick={ handleClick }
                >
                    Volver
                </button>
            </div>
        </div>
    )
}
