import React, { useState } from 'react'
import { HeroFinder } from './HeroFinder';

export const HomeScreen = ({ history }) => {
    const [state, setState] = useState('');
    const handleInputChange = (e) => {
        setState(e.target.value)
    }
    const handleSubmit = (e) => {
        e.preventDefault();
        history.push(`?=${state}`)
    }
    
    return (
        <div>
            <h1>Home Screen</h1>
            <form>
            <div className="input-group mb-3">
                <input type="text" className="form-control" placeholder="Find a Hero" aria-label="Find a Hero" value={ state } onChange={ handleInputChange }/>
                <button className="btn btn-outline-secondary" type="submit" onClick={ handleSubmit }>Search</button>
            </div>
            </form>
            <HeroFinder inputEntry={state} />
        </div>
    )
}
