import React from 'react';
import { Redirect, Route } from 'react-router-dom';
import PropTypes from 'prop-types';

// It render if user is loggen in: podría hacer una forma condicional de renderizado en el componente pero así queda más organizado (por un lado el componente en sí y por el otro el manejo de rutas)
export const PrivateRoute = ({ isLogged, component: Component, ...rest }) => {
    isLogged && localStorage.setItem('lastPath', rest.location.pathname); // save last path
    return (
        <>
            {/* Tener en cuenta que esta forma de plantear el renderizado condicional es diferente a la del curso:
            Fernando lo hace directamente con los componentes (como se puede ver en PublicRoute.js). La forma de fernando deja el Route fijo y dentro de el hace el condicional entre componentes.
            Mientras que yo renderizo condicionalmente o el Route o el redirect. Aunque funciona, no estoy seguro de si eso está conceptualmente bien */}
            {
                (isLogged)
                    ? (
                        <Route {...rest} component={Component}>
                        </Route>
                    )
                    : (
                        <Redirect to='/login' />
                    )
            }
        </>
    )
}

PrivateRoute.propTypes = {
    isLogged: PropTypes.bool.isRequired,
    component: PropTypes.func.isRequired
}
