import React, { useContext } from 'react'
import { Link, useHistory } from 'react-router-dom';
import { AuthContext } from '../auth/AuthContext';
import { types } from '../types/types';

export const Navbar = () => {
    const { userState, dispatch } = useContext(AuthContext)
    const history = useHistory();
    
    const handleLogout = () => {
        dispatch({
            type: types.logout,
            payload: {
                user: "",
                logged: false
            }
        })
        history.replace('/login')
    }
    
    return (
        <div>
            <nav className="navbar navbar-expand-lg navbar-light bg-light">
                <div className="container-fluid">
                    <Link className="navbar-brand" to="/">HeroesApp</Link>
                    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarNavDropdown" aria-controls="navbarNavDropdown" aria-expanded="false" aria-label="Toggle navigation">
                    <span className="navbar-toggler-icon"></span>
                    </button>
                    <div className="collapse navbar-collapse" id="navbarNavDropdown">
                    <ul className="navbar-nav">
                        <li className="nav-item">
                            <Link className="nav-link active" aria-current="page" to="/marvel">Marvel</Link>
                        </li>
                        <li className="nav-item">
                            <Link className="nav-link" to="/dc">DC</Link>
                        </li>                 
                    </ul>
                    <ul className="navbar-nav ms-auto">
                        {( userState.logged &&
                            <li className="nav-item">
                                <span className="nav-link text-info">{userState.user}</span>
                            </li>
                        )}
                        <li className="nav-item">
                            <button id="logout-btn" className="nav-link btn" onClick={ handleLogout }>Logout</button>
                        </li>                        
                    </ul>
                    </div>
                </div>
            </nav>
        </div>
    )
}
